@import "../../../scss/libs/normalize.scss";
@import "../../../scss/libs/variable.scss";

.wrapper{
    overflow: hidden;
    padding-top: 56px;
    position: relative;
    display: flex;
    flex-direction:column;
    width: 1200px;
    max-height: 1000px;
    margin: 0 auto;
    @media (max-width: 320px) {
        max-height: 800px;
        width: 320px;
        padding-top: 23px;
    }
}
.AdvertiserOrder{
    left: -8px;
    position: relative;
    top: 99px;
    width: 1102px;
    height:545px;
    margin: 0 auto;
    background-color: rgba(255, 118, 0, 0.2);
    border-radius: 30px;
    box-shadow: 5px 5px 7px rgba(0, 0, 0, 0.10);
    @media (max-width: 320px) {
        left: 0px;
        top: 84px;
        width: 100%;
        height: 308px;
        border-top-right-radius: 0px;
        border-bottom-right-radius: 0px;
    }
}

.statistics{
    padding-top: 200px;
     padding-left: 77px;
     @media (max-width: 320px) {
         padding-top: 133px;
         padding-left: 0px;
     
     }
 }

 .links{
    padding-top: 40px;
    padding-left: 46px;
    display: flex;
    gap:50px;
    font-size: 20px;
    line-height: 23.44px;
    @media (max-width: 320px) {
        gap:6px;
        padding-top:4px;
        padding-left: 10px;
    }
    &Right{
        display: flex;
        flex-direction: column;
        gap: 6px;
        letter-spacing: .2px;
        line-height: 23.44px;
        @media (max-width: 320px) {
            gap:5px;
            line-height: 14.06px;
            >div>a{
                font-size: 12px;
                letter-spacing:-.3px;
            }
        }
    
       
    }
    &Middle{
        padding-left:38px;
        display: flex;
        flex-direction: column;
        gap: 6px;
        line-height: 23.44px;
        &Active{
            text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
            color: #FF3600;
            letter-spacing: 1px;
         }
        @media (max-width: 320px) {
            padding-left: 57px;
            gap:3px;
            line-height: 14.06px;
            >div>a{
                font-size: 12px;
                letter-spacing:-.3px;
            }
        } 
    }
    &Left{
        display: flex;
        flex-direction: column;
        gap: 30px;
        line-height: 23.44px;
        padding-left:0px;
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        letter-spacing: .9px;
        >div>a{
            color: #FF3600;
        }
        @media (max-width: 320px) {
            padding-top: 18px;
            padding-left:20px;
            gap:3px;
            line-height: 14.06px;
            >div>a{
                font-size: 12px;
                letter-spacing:-.3px;
            }
        }
    }
 }
 .change{
    padding-top: 9px;
    padding-left: 45px;
    text-decoration: underline;
    line-height: 21px;
    width: 189px;
    cursor: pointer;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    @media (max-width: 320px) {
        line-height: 14px;
        padding-top: 1px;
        padding-left:10px;
        font-size: 12px;
        width:90px;
     }
    
 }

 .table{
    padding-left:46px;
    padding-bottom: 50px;
    padding-top:31px;
    @media (max-width: 320px) {
        padding-top: 0px;
        padding-left:9px;
        display: block;
        overflow-x: scroll;
        white-space: nowrap;
        }
  
    &Head{
        position: relative;
        display: grid;
        grid-template-columns: 190px 152px 109px 155px 182px ;
        grid-template-rows:30px;
        @media (max-width: 320px) {
            grid-template-columns: 120px 58px 66px 104px 118px;
            grid-template-rows:20px;
            
        }
        >div{
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align:center;
            background:  rgb(240,158,86, 0.3);
            border: 1px solid rgba(13, 13, 13, 0.3);
           font-weight: $weight-700;
           @media (max-width: 320px) {
             font-size: 12px;
            line-height: 14px;
        }
        }
     
    }
    &Body {
        position: relative;
        display: grid;
        grid-template-columns: 190px 152px 109px 155px 182px ;
        grid-template-rows:30px;
        @media (max-width: 320px) {
            grid-template-columns: 120px 58px 66px 104px 118px;
            grid-template-rows:20px;
            
        }
    >div{
    
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align:center;
        border: 1px solid rgba(13, 13, 13, 0.3);
        font-weight: $weight-400;
        font-size: 16px;
        line-height: 18.75px;
        @media (max-width: 320px) {
            font-size: 12px;
           line-height: 14px;
       }
        }
    }
           
    
}
 
